* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 100px;
}

.country-list__item {
  display: flex;
  text-decoration: none;
  margin-bottom: 10px;
}

.item__image {
 margin-right: 20px; 
}
.item__text {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
}

.country-info__item {
  display: flex;
  text-decoration: none;
  margin-bottom: 10px;
}

.head__text {
  margin: 0;
  font-size: 40px;
  font-weight: bold;
}

